#home-screen {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  border-width: 0; }

#home-screen h1 {
  height: 300px;
  text-align: center;
  padding-top: 100px;
  font-size: 70px; }

@media screen and (max-width: 450px) {
  #home-screen h1 {
    height: 150px;
    padding-top: 50px;
    font-size: 50px; } }

#home-footer {
  position: fixed;
  width: 100%;
  text-align: center;
  color: white;
  bottom: 100px; }

@media screen and (max-width: 450px) {
  #home-footer {
    bottom: 20px; } }

#home-footer p {
  font-size: 10px; }



