html, body {
  height: 100%; }

body {
  background-color: #3399ff; }

@media screen and (min-width: 450px) {
  body {
    background: -webkit-linear-gradient(#ffffff, #3399ff);
    /* For Safari 5.1 to 6.0 */
    background: -o-linear-gradient(#ffffff, #3399ff);
    /* For Opera 11.1 to 12.0 */
    /* For Firefox 3.6 to 15 */
    background: -webkit-gradient(linear, left top, left bottom, from(#ffffff), to(#3399ff));
    background: linear-gradient(#ffffff, #3399ff);
    /* Standard syntax */ } }



