#ehr-summary {
  background-color: #ffffff;
  border-radius: 8px;
  padding: 10px 5px;
  text-align: center; }

#ehr-summary h4 {
  margin-bottom: 15px; }

#ehr-summary .ehr-section, #ehr-summary .header, #ehr-summary .footer {
  width: 100%;
  display: block;
  margin: 0; }

#ehr-summary .header {
  color: #BD0FE1;
  font-size: 12px; }

#ehr-summary .footer {
  color: #1f1f1f;
  font-size: 15px; }



