#patient-picker-wrapper {
  width: 100%; }

#patient-picker-wrapper input {
  margin: 0 auto;
  height: 30px;
  width: 400px; }

@media screen and (max-width: 450px) {
  #patient-picker-wrapper input {
    width: 100%; } }

#patient-picker {
  margin: 0 auto;
  max-height: 200px;
  overflow-y: scroll;
  border-radius: 5px;
  padding-bottom: 0px;
  background-color: #ffffff;
  width: 400px; }

@media screen and (max-width: 450px) {
  #patient-picker {
    width: 100%; } }

#patient-picker .list-group {
  margin-bottom: 3em;
  width: 100%; }

#patient-picker li {
  border-bottom: 0;
  text-align: center; }

#patient-picker li:hover {
  padding: 15px 23px;
  background-color: #333;
  color: #fff; }

#patient-picker a {
  text-decoration: none;
  color: #333; }

#patient-picker:after {
  content: "";
  position: absolute;
  z-index: 99;
  bottom: 0;
  pointer-events: none;
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 255, 255, 0)), color-stop(90%, white));
  background-image: -webkit-linear-gradient(top, rgba(255, 255, 255, 0), white 90%);
  background-image: -o-linear-gradient(top, rgba(255, 255, 255, 0), white 90%);
  background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0), white 90%);
  height: 3em;
  margin: 0 auto;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  width: 400px; }

@media screen and (max-width: 450px) {
  #patient-picker:after {
    width: 0; } }



