.findings .list-group-item {
  background: transparent;
  display: inline-block;
  width: 100%; }

.findings .list-group-item .left {
  width: 50%;
  float: left; }

.findings .list-group-item .right {
  width: 30%;
  float: left;
  padding-left: 15px; }

.findings .list-group-item .trash {
  width: 10%;
  float: right;
  color: red;
  padding-top: 4px; }

.findings .list-group-item .trash span:hover {
  cursor: pointer; }



